export default {
	data() {
		return {
			popupStatus: false,
			name: '',
			nameStatus: false,
			namePlaceholder: '请输入您的姓名',
			phone: '',
			phoneStatus: false,
			phonePlaceholder: '请输入您的电话号码',
			passWord: '',
			passWordStatus: false,
			passWordPlaceholder: '请输入您的密码',
			PlaceholderNull: '此栏位不得为空'
		};
	},
	watch:{
		name: function() {
			if (!this.name) {
				this.nameStatus = true
				this.namePlaceholder = this.PlaceholderNull
			} else {
				this.nameStatus = false
				this.namePlaceholder = "请输入您的姓名"
			}
		},
		phone: function() {
			if (!this.phone) {
				this.phoneStatus = true
				this.phonePlaceholder = this.PlaceholderNull
			} else {
				this.phoneStatus = false
				this.phonePlaceholder = "请输入您的电话号码"
			}
		},
		passWord: function() {
			if (!this.passWord) {
				this.passWordStatus = true
				this.passWordPlaceholder = this.PlaceholderNull
			} else {
				this.passWordStatus = false
				this.passWordPlaceholder = "请输入您的密码"
			}
		}
	},
	methods:{
		toSkip () {
			this.popupStatus = false
			uni.redirectTo({
				url: '../../pages/memberLogin/memberLogin'
			})
		},
		submit () {
			// 判断名字是否为空
			if (!this.name) {
				this.nameStatus = true
				this.namePlaceholder = this.PlaceholderNull
			} else {
				this.nameStatus = false
				this.namePlaceholder = "请输入您的姓名"
			}
			// 判断电话是否为空
			if (!this.phone) {
				this.phoneStatus = true
				this.phonePlaceholder = this.PlaceholderNull
			} else {
				this.phoneStatus = false
				this.phonePlaceholder = "请输入您的电话号码"
			}
			// 判断密码是否为空
			if (!this.passWord) {
				this.passWordStatus = true
				this.passWordPlaceholder = this.PlaceholderNull
			} else {
				this.passWordStatus = false
				this.passWordPlaceholder = "请输入您的密码"
			}
			// 必填都填了执行提交并跳转至登录
			if (this.name && this.phone && this.passWord) { 
				 // 加载动画
				uni.showLoading({
					title: '注册中'
				});
				this.$member.post(
					'Store/register',
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="name"' +
					'\r\n' +
					'\r\n' + this.name +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="password"' +
					'\r\n' +
					'\r\n' + this.passWord +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="phone"' +
					'\r\n' +
					'\r\n' + this.phone +
					'\r\n--XXX--').then(res => {
					// 关闭加载动画
					uni.hideLoading();
					if (res.data.code == 200) {
						this.popupStatus = true
					} else {
						 uni.showToast({
							icon: 'none',
							title: res.data.msg,
							duration: 2000
						 })
					}
				})
			}
		}
	}
}
